  --hero-bg: #191c26;
  --overlay-color: rgba(25, 28, 38, .2);
  --prices-bg-color: #111319;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

p, h1, h2, h3, h4, h5, h6 {
